import{noop,isPromise}from'./basic';export function callInterceptor(interceptor,{args=[],done,canceled,error}){if(interceptor){// eslint-disable-next-line prefer-spread
const returnVal=interceptor.apply(null,args);if(isPromise(returnVal)){returnVal.then(value=>{if(value){done();}else if(canceled){canceled();}}).catch(error||noop);}else if(returnVal){done();}else if(canceled){canceled();}}else{done();}}