export function noop(){}export const extend=Object.assign;export const inBrowser=typeof window!=='undefined';// eslint-disable-next-line
export const isObject=val=>val!==null&&typeof val==='object';export const isDef=val=>val!==undefined&&val!==null;// eslint-disable-next-line @typescript-eslint/ban-types
export const isFunction=val=>typeof val==='function';export const isPromise=val=>isObject(val)&&isFunction(val.then)&&isFunction(val.catch);export const isDate=val=>Object.prototype.toString.call(val)==='[object Date]'&&!Number.isNaN(val.getTime());export function isMobile(value){value=value.replace(/[^-|\d]/g,'');return /^((\+86)|(86))?(1)\d{10}$/.test(value)||/^0[0-9-]{10,13}$/.test(value);}export const isNumeric=val=>typeof val==='number'||/^\d+(\.\d+)?$/.test(val);export const isIOS=()=>inBrowser?/ios|iphone|ipad|ipod/.test(navigator.userAgent.toLowerCase()):false;export function get(object,path){const keys=path.split('.');let result=object;keys.forEach(key=>{result=isObject(result)?result[key]??'':'';});return result;}export function pick(obj,keys,ignoreUndefined){return keys.reduce((ret,key)=>{if(!ignoreUndefined||obj[key]!==undefined){ret[key]=obj[key];}return ret;},{});}export const isSameValue=(newValue,oldValue)=>JSON.stringify(newValue)===JSON.stringify(oldValue);export const toArray=item=>Array.isArray(item)?item:[item];